@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Cormorant+Garamond:300|Lora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  /* outline: 0.5px solid black; */
  font-family: Open Sans;
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.fp-tableCell {
  vertical-align: 0; }

#background {
  height: 100vh;
  width: 100vw;
  position: relative;
  background-image: url(/delivery-react/static/media/background.e7993762.png);
  background-size: cover;
  background-repeat: no-repeat; }

#intro {
  width: 40%;
  position: relative;
  left: 150px;
  top: 20px;
  color: white; }
  #intro p {
    font-size: 11px; }
  #intro h1 {
    font-family: "Cormorant Garamond", "serif";
    margin: 20px 0;
    font-size: 45px; }
  #intro button {
    background-color: transparent;
    width: 100px;
    height: 30px;
    border: 1px solid white;
    border-radius: 2px;
    color: white;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out; }
    #intro button:hover {
      cursor: pointer;
      background-color: #5e5e5e;
      -webkit-transition: all 0.3s ease-in;
      transition: all 0.3s ease-in; }

nav {
  position: fixed;
  width: 100vw;
  font-size: 11px;
  padding: 23px 40px;
  z-index: 10; }
  nav span {
    display: inline-block;
    color: white; }
  nav ul {
    float: right;
    list-style-type: none; }
    nav ul li {
      display: inline-block;
      margin-left: 60px;
      color: white;
      cursor: pointer;
      background: -webkit-gradient(linear, left top, right top, from(#adadad), to(#adadad)) center bottom no-repeat;
      background: -webkit-linear-gradient(left, #adadad 0%, #adadad 100%) center bottom no-repeat;
      background: linear-gradient(to right, #adadad 0%, #adadad 100%) center bottom no-repeat;
      -webkit-transition: all 0.3s ease-out;
      transition: all 0.3s ease-out;
      padding-bottom: 3px;
      background-size: 0 1px; }
      nav ul li:hover, nav ul li.active {
        background-size: 100% 1px;
        -webkit-transition: all 0.3s ease-in;
        transition: all 0.3s ease-in; }

.dark span, .dark ul li {
  color: black; }

#info {
  height: 100vh;
  width: 100vw;
  position: relative; }

#form-container, #map-container {
  position: relative;
  display: inline-block;
  width: 50%;
  height: 100%; }

#map-container {
  float: left;
  padding: 65px 70px 40px 40px; }

#form-container {
  float: right;
  display: flex;
  align-items: center;
  padding: 75px 150px; }

#map {
  height: 100%;
  box-shadow: 2px 2px 20px grey;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out; }
  #map:hover {
    box-shadow: 10px 10px 50px grey;
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in; }

#form label {
  display: block;
  margin: 20px 0;
  font-size: 11px; }

#form input, #form select {
  -webkit-appearance: none;
  margin-top: 5px;
  padding-left: 8px;
  width: 300px;
  height: 35px;
  border: 1px solid black;
  border-radius: 0;
  background-color: white; }

#form .date {
  padding-left: 40px; }
  #form .date-span {
    position: relative;
    right: 291px; }

#form .time-start {
  margin-right: -29px;
  width: 130px;
  padding-left: 45px; }
  #form .time-start-span {
    position: relative;
    right: 92px; }

#form .time-end {
  margin-right: -29px;
  width: 113px;
  padding-left: 27px; }
  #form .time-end-span {
    position: relative;
    right: 76px; }

#form #quote {
  position: relative;
  left: 20px; }

#form .name {
  width: 90px;
  margin-right: 6px; }

#form .phone {
  width: 108px; }

#form .submit {
  background-color: #f5f5f5;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out; }
  #form .submit:hover {
    background-color: #c4c4c4;
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in; }

.none {
  display: none !important; }

.next {
  text-align: center;
  color: #afafaf; }
  .next-ani {
    font-size: 15px;
    text-align: center;
    -webkit-animation-name: nextAni;
            animation-name: nextAni;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    position: relative; }

@-webkit-keyframes nextAni {
  0% {
    top: 0px; }
  50% {
    top: 30px; }
  100% {
    top: 0px; } }

@keyframes nextAni {
  0% {
    top: 0px; }
  50% {
    top: 30px; }
  100% {
    top: 0px; } }

#thanks {
  font-size: 11px;
  position: relative;
  bottom: 200px; }

